    },
    mounted(){
      this.kopLamp()
      this.pijlLinks()
    },
  methods:{

      pijlLinks: function() {

          //console.log(this.inputkoplamp)
           if(this.buttonLeft == 1){

            this.colorpijlLinks = '#ff0048';          
           

          }else if(this.buttonLeft  == 0){
                this.colorpijlLinks = '#1A1A1A';
               
          }
          window.requestAnimationFrame(this.pijlLinks)
      },
      pijlRechtsDown: function() {

          if(this.StateRechtsPijl == false){
            this.colorpijlRechts = '#ff0048';          
            this.StateRechtsPijl = true

          }else if(this.StateRechtsPijl == true){
                this.colorpijlRechts = '#1A1A1A';
                this.StateRechtsPijl = false
          }
      },
      kopLamp: function() {
        //console.log(this.inputkoplamp)
           if(this.inputkoplamp == 1){
               console.log("het werkt")
            this.colorKoplamp = '#ff0048';          
           

          }else if(this.inputkoplamp  == 0){
                this.colorKoplamp = '#1A1A1A';
               
          }
          window.requestAnimationFrame(this.kopLamp)
      },

    }
}
